import { createSlice, configureStore } from '@reduxjs/toolkit'

const languageSlice = createSlice({
    name: "language",
    initialState: {language: "Portuguese"},
    reducers: {
        changeEnglish(state){state.language = "English"},
        changePortuguese(state){state.language = "Portuguese"}
    }
})

export const { changeEnglish, changePortuguese } = languageSlice.actions;

const store = configureStore({
    reducer: languageSlice.reducer
})

export default store;