import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';

import { useDispatch, useSelector } from "react-redux"
import { changeEnglish } from '../store';
import { changePortuguese } from '../store';

import "./header.css"

let headshot = require("../imgs/headshot.png")
const linkedin = require("../imgs/linkedin.png");
const github = require("../imgs/github.png");
 
function Header() {

    const dispatch = useDispatch();
    const currentLanguage = useSelector((state: any) => state.language)

    function copyEmailToClipboard() {
        navigator.clipboard.writeText("guilhermebazenga@gmail.com");
        alert("Copied to clipboard!");
    } 

    function copyPhoneToClipboard() {
        navigator.clipboard.writeText("+55 (61) 99932-1968");
        alert("Copied to clipboard!");
    }
    
    function languageChange(selection: string){
        if(selection === "Portuguese"){
            dispatch(changePortuguese());
        }
        if(selection === "English"){
            dispatch(changeEnglish());   
        }
    }

    return <section id="header">
        <div className='light-ball'></div>
        <div className='squares'></div>
        <div id="info-area">
            <img src={headshot} alt="author-headshot"></img>
            <p onClick={copyEmailToClipboard}>guilhermebazenga@gmail.com</p>
            <p onClick={copyPhoneToClipboard}>+55 (61) 99932-1968</p>
            <a href='https://www.linkedin.com/in/guilhermebazengacoder/' target="_blank" rel="noreferrer"><img src={linkedin} alt="linkedin"></img></a>
            <a href='https://github.com/Gbazenga/' target="_blank" rel="noreferrer"><img src={github} alt="github"></img></a>
        </div>
        <div id="opening-letter">
            <hr/>
            <Container>
                <Button className='btn' onClick={() => languageChange("Portuguese")}>Português</Button>
                <Button className='btn' onClick={() => languageChange("English")}>English</Button>
                {currentLanguage === "Portuguese" && <p>O meu nome é Guilherme Bazenga, sou um Desenvolvedor de web júnior com perto de dois anos de experiência e depois de passar algum tempo aprendendo Javascript e ReactJs, estou pronto para o meu próximo desafio. Além de pagar as contas, procuro um emprego que me desafie, e que me obrigue a dar o meu melhor mesmo sobre pressão. Estou sempre aprendendo e melhorando e estou animado para ver o que o futuro traz! Role a tela para baixo para ver alguns dos meus projetos e verificar minhas certificações.</p>}
                {currentLanguage === "English" && <p>My name is Guilherme Bazenga, I’m a junior level web developer with a couple of years of experience under my belt and after spending some time learning Javascript and ReactJs, I’m ready for my next challenge. Outside of paying the bills, I’m looking for a position that will make me rise to the occasion and really force me to excel under pressure. I’m forever learning and improving and am excited to see what the future brings! Scroll down to see some of my work and check my certifications. </p>}
            </Container>
            <hr/>
        </div>
    </section>
}

export default Header;