import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';

import AOS from "aos";
import 'aos/dist/aos.css';

import { useSelector } from "react-redux"

import "./websiteItem.css"

let image1 = require("../imgs/travel.jpg")
let image2 = require("../imgs/wordle.jpg")
let image3 = require("../imgs/book.jpg")
let image4 = require("../imgs/cookie.jpg")

const websiteInfo = [
  {
    key: 1,
    title: "Travel CO.", 
    description: "A simple “travel service” based website focused on smooth transitions between elements and pages. Built with the intent to be attention-grabbing and fun to play around with, it’s a good show of UI design and technology in one package. ",
    descriptionPT: "Um website baseado em “serviço de viagem”, focado em transiçoes suaves entre elementos e páginas. Contruído com a intenção de chamar a atenção do usuário e ser divertido de navegar. É uma boa demonstração de design de UI e tecnologia em um único pacote.",
    link: "https://gbazenga.github.io/travel-agency-project/",
    image: image1
  },
  {
    key: 2,
    title: "Wordle Clone", 
    description: "Maybe I just like wordle a little too much. It sounded like it’d be a decent coding challenge and I was right. If you want to do more than one wordle puzzle  day, this is the website for you. ",
    descriptionPT: "Bem, talvez eu goste do Wordle um pouco demais. Olhando para ele, achei que poderia ser um bom desafio de coding, e estava correto. Se você quiser fazer mais que uma ronda de wordle por dia, este é o website para você.",
    link: "https://gbazenga.github.io/Wordle-Project/",
    image: image2
  },
  {
    key: 3,
    title: "Book Browser", 
    description: "My attempt at the “make a storefront for your portfolio” advice that everyone has. I wanted to change it up and make something a little more unique so I came up with this. It has most of the same elements a e-store would have, just packaged differently.",
    descriptionPT: "Minha resposta ao conselho “faça uma online shop para seu portfólio” que todo mundo tem. Eu queria fazer algo um pouco mais único, então criei este site. Tem a maioria dos elementos que uma loja virtual teria, apenas tendo uma abordagem diferente.",
    link: "https://gbazenga.github.io/book-browser-project/",
    image: image3
  },
  {
    key: 4,
    title: "Cookie Clicker Clone", 
    description: "A very simply designed clone of cookie clicker. This one was all about functionality and practicing my coding logic. Turned out alright! Making the incrementing smooth took me a minute though...",
    descriptionPT: "Um clone de cookie clicker de design muito simples. Esse foi tudo sobre funcionalidade e prática de minha lógica de coding. Acabou tudo bem! Para o incremento ficar top levou um minuto ...",
    link: "https://gbazenga.github.io/cookie-clicker-project/",
    image: image4
  }
]

function WebsiteItem() {
    AOS.init();

    const currentLanguage = useSelector((state: any) => state.language)

    return <div className="website-items container" >
      {websiteInfo.map((website) => (
        <div key={website.key} className='website-item'>
        {website.key % 2 === 0 && <Row>
          <Col  xs={12} lg={5}>
            <img src={website.image} alt="website homepage" className='image-left' data-aos="fade-right"></img>
          </Col> 
          <Col  xs={12} lg={7} className='text-area'>
            <div className='text-format text-left'>

            {currentLanguage === "English" && <div data-aos="fade-left"><h2>{website.title}</h2>
            <p>{website.description}</p>
            <a href={website.link} target="_blank"><Button>View</Button></a></div>}

            {currentLanguage === "Portuguese" && <div data-aos="fade-left"><h2>{website.title}</h2>
            <p>{website.descriptionPT}</p>
            <a href={website.link} target="_blank"><Button>Visitar</Button></a></div>}
            </div>
          </Col>  
          <hr/>
        </Row>
        
        }

        {website.key % 2 !== 0 && <Row>
          <Col  xs={12} lg={7} className='text-area'>
            <div className='text-format'>
            {currentLanguage === "English" && <div data-aos="fade-right"><h2>{website.title}</h2>
            <p>{website.description}</p>
            <a href={website.link} target="_blank"><Button>View</Button></a></div>}

            {currentLanguage === "Portuguese" && <div data-aos="fade-right"><h2>{website.title}</h2>
            <p>{website.descriptionPT}</p>
            <a href={website.link} target="_blank"><Button>Visitar</Button></a></div>}
            </div>
          </Col>  
          <Col  xs={12} lg={5}>
            <img src={website.image} alt="website homepage" className='image-right' data-aos="fade-left"></img>
          </Col> 
          <hr/>
        </Row>}
        
        </div>
      ))}
    </div>
}

export default WebsiteItem;