import { useSelector } from "react-redux";
import Carousel from 'react-bootstrap/Carousel';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';

import AOS from "aos";
import 'aos/dist/aos.css';

import "./certifications.css"

let gama = require("../imgs/certifications/gama-full-stack.jpg")
let javascript = require("../imgs/certifications/udemy-javascript.jpg")
let npm = require("../imgs/certifications/udemy-npm.jpg")
let react = require("../imgs/certifications/udemy-react.jpg")
let tests = require("../imgs/certifications/udemy-tests.jpg")
let typescript = require("../imgs/certifications/udemy-typescript.jpg")
let cSharp = require("../imgs/certifications/c-sharp.jpg")
let next = require("../imgs/certifications/udemy-next.jpg")

const certificationInfo = [
    {
        key: "cert1",
        img: gama
    },
    {
        key: "cert2",
        img: npm
    },
    {
        key: "cert3",
        img: javascript
    },
    {
        key: "cert4",
        img: react
    },
    {
        key: "cert5",
        img: typescript
    },
    {
        key: "cert6",
        img: tests
    },
    {
        key: "cert7",
        img: cSharp
    },
    {
        key: "cert8",
        img: next
    }
]

function Certifications() {
    AOS.init();

    const currentLanguage = useSelector((state: any) => state.language)

    function modalToggle() {
        const modal = document.getElementsByClassName("bg-modal")[0];
        modal.classList.toggle("modal-closed")
    }

    return<section id="certifications">
        <div className="bg-modal modal-closed" onClick={modalToggle}>
            <div className="cert-modal container">
                {certificationInfo.map((item) => (
                    <img key={item.key} src={item.img} alt="site"/>
                ))}
            </div>
        </div>

        <div className="container">
        {currentLanguage === "English" && <h1>Certifications</h1>}
        {currentLanguage === "Portuguese" && <h1>Certificações</h1>}
            <Row>
                <Col  xs={12} lg={6}>
                    {currentLanguage === "English" && <div className="certifications-info" data-aos="fade-up-right">
                    <p>Here is a full list of all my current certifications. More will be added with time as I learn. Click below for a full, detailed list.</p>
                    <a href="#" onClick={modalToggle}><Button>View full list</Button></a></div>
                    }

                    {currentLanguage === "Portuguese" && <div className="certifications-info" data-aos="fade-up-right">
                    <p>Aqui tem uma lista completa de minhas certificações. Mais serão adicionadas com tempo, enquanto continuo o aprendizado. Dê um click no botão abaixo para uma lista completa.</p>
                    <a href="#" onClick={modalToggle}><Button>Ver lista completa</Button></a></div>
                    }
                </Col>
                <Col  xs={12} lg={6}>
                    <Carousel data-aos="fade-up-left">
                        {certificationInfo.map((item) => (
                            <Carousel.Item key={item.key} interval={2000}>
                                <img src={item.img} alt="site"></img>
                            </Carousel.Item>
                        ))}
                    </Carousel>
                </Col>
            </Row>
        </div>
    </section>
}

export default Certifications;